.App {
  /* font-family: sans-serif; */
  text-align: center;
}

.App-header {
  background-color: #222;
  color: white;
  font-size: 1.25rem;
  margin: 0;
  padding: 1rem;
}

.tickers {
  display: inline-flex;
  list-style-type: none;
  padding: 0;
}

.cryptocurrency {
  padding: 1rem 2rem 2rem;
  margin: 1rem;
  border-radius: 0.5rem;
  border: 1px solid grey;
}
.cryptocurrency p {
  color: maroon;
}
.currency-title {
  font-size: 1.5rem;
}
.coin-color {
  background-color: #fbe2c3;
  border-bottom: solid 4px #f7931a;
}
